<template>
  <div class="contact-wrapper" ref="contactContainer">
    <app-bg
        :height="187" src="http://osbfr39w7.bkt.clouddn.com/wface/contact/4_02-min.jpg"/>

      <div class="form-box">
          <div class="form-body">
              <div class="form-row f-row-between">
                  <div class="form-row f-row-start" style="width:62%;">
                      <label class="f-mr16">您的姓名:</label>
                      <input type="text" class="form-input" />
                  </div>
                  <div class="form-row half f-row-between">
                      <div class="f-row-between radio-item" style="width: 0.46rem; margin-right: 20px">
                        <label for="M">先生</label>
                        <span class="simulation" :style="{backgroundColor: sex === 'M' ? '#fb3a6f' : '#f5f1ee', borderWidth: sex === 'M' ? 0 : '2px'}"></span>
                        <input type="radio" value="M" v-model="sex"/>
                      </div>
                      <div class="f-row-between  radio-item" style="width: 0.46rem;">
                        <label for="F">女士</label>
                        <span class="simulation" :style="{backgroundColor: sex === 'F' ? '#fb3a6f' : '#f5f1ee', borderWidth: sex === 'F' ? 0 : '2px'}"></span>
                        <input type="radio" value="F" v-model="sex"/>
                      </div>
                  </div>
              </div>
              <div class="form-row f-row-start f-mt20">
                  <label class="f-mr16">联系方式:</label>
                  <input type="text" class="form-input" />
              </div>
              <div class="form-row f-row-start f-mt20">
                  <label class="f-mr16">开店预算:</label>
                  <input type="text" class="form-input" />
              </div>
              <div class="form-row f-row-start f-mt20" style="alignItems: flex-start">
                  <label class="f-mr16">咨询内容:</label>
                  <textarea class="form-textarea"></textarea>
              </div>
              <div class="submit-btn-wrap f-row-center">
                  <button type="button" name="button" @touchstart.stop="submit">提交留言</button>
              </div>
          </div>
      </div>

      <div class="blank">

      </div>

  </div>
</template>

<script>
import AppBg from '@/components/AppBg'
import axios from 'axios'
export default {
  data() {
    return {
      sex: 'M',
       screenHeight: 0
    }
  },
  name: 'contact',
  created() {
  },
  mounted() {
    window.addEventListener('resize', () => this.screenHeight = window.screen.height)
    this.$nextTick(() => {
        this.screenHeight = window.screen.height
        console.log(window.getComputedStyle(this.$refs.contactContainer).height)
        console.log(this.screenHeight)
    })
  },
  methods:{
    submit() {
      axios.post('/user', {})
        .then(function (response) {
          console.log(response);
        })
        .catch(err => console.log(err))
    }
  },
  components: {
    AppBg
  }
}
</script>


<style scoped lang="less">
.contact-wrapper{
  margin-top: -12px;
  .form-box{
      width: 100%;
      padding: 20px 10px;
      height: 3rem;
      background-color: #f7f6f1;
  }
  .form-body{
    padding: 10px;
    width: 100%;
    height: 100%;
    border: 1px solid #7a7977;
  }
  .f-mt20{
    margin-top: 0.2rem;
  }
  .f-mr16{
    margin-right: 0.16rem;
  }
  input[type=radio]{
    position: relative;
    opacity: 0;
    z-index: 5;
  }
  .radio-item{
    position: relative;

  }
  .simulation{
    position: absolute;
    width: 0.1rem;
    height: 0.1rem;
    border-radius: 50%;
    background-color: #fb3a6f;
    right: 0;
    top: 0.02rem;
    border: 2px solid #8e8d89;
  }
  .form-row{
    label{
      font-size: 0.14rem;
      color: #898884;
    }
    input[type=text]{
      width: 1.3rem;
      height: 0.24rem;
      font-size: 0.14rem;
      padding-left: 0.05rem;
      background-color: #f7f6f1;
      border: 1px solid #93928e;
    }
  }
  .form-textarea{
      flex: 1;
      font-size: 0.14rem;
      padding: 0.05rem;
      border: 1px solid #93928e;
      background-color: #f7f6f1;
      height: 0.6rem;
  }
  .blank{
    height: 120px;
    background-color: #f7f6f1;
  }
  .submit-btn-wrap{
    margin-top: 0.16rem;
    button{
      font-size: 0.15rem;
      color: #fff;
      background-color: #ff3a6f;
      border:0;
      padding: 0.06rem;
      font-weight: lighter;
    }
  }
}
</style>



